'use client';

import { useParams } from 'next/navigation';
import { useEffect, useState, useRef } from 'react';
import Image from 'next/image';
import Link from 'next/link';
import { getArtistBySlug, getStrapiImageUrl, Artist } from '@/lib/strapi';

// Sample artist data for demo
const sampleArtistData: Record<string, any> = {
  'sinca': {
    name: 'Sinca',
    bio: 'Sinca is a San Francisco-based DJ and producer known for his deep, groovy house sets that blend soulful melodies with driving rhythms. With a career spanning over a decade, he has become a staple in the Bay Area underground scene.',
    image: '/images/Sinca.jpeg',
    soundcloud: 'https://w.soundcloud.com/player/?url=https%3A//api.soundcloud.com/playlists/1234567&color=%23ff5500&auto_play=false&hide_related=false&show_comments=true&show_user=true&show_reposts=false&show_teaser=true',
    video: 'https://www.youtube.com/embed/dQw4w9WgXcQ',
    socialLinks: {
      soundcloud: 'https://soundcloud.com/sinca',
      instagram: 'https://instagram.com/sinca',
      residentAdvisor: 'https://ra.co/dj/sinca'
    }
  },
  'camea': {
    name: 'Camea',
    bio: 'Camea is an American DJ and producer who has been a driving force in the electronic music scene. Known for her eclectic sets that seamlessly blend techno, house, and minimal, she has performed at major festivals and clubs worldwide.',
    image: '/images/camea.png',
    soundcloud: 'https://w.soundcloud.com/player/?url=https%3A//api.soundcloud.com/playlists/1234567&color=%23ff5500&auto_play=false&hide_related=false&show_comments=true&show_user=true&show_reposts=false&show_teaser=true',
    socialLinks: {
      soundcloud: 'https://soundcloud.com/camea',
      instagram: 'https://instagram.com/camea',
      residentAdvisor: 'https://ra.co/dj/camea'
    }
  },
  'atish': {
    name: 'Atish',
    bio: 'Atish is a DJ and producer known for his deep, melodic house sets that create an emotional journey on the dancefloor. His productions have been released on labels like Kindisch, Stil Vor Talent, and his own Manjumasi label.',
    image: '/images/mainone.png',
    soundcloud: 'https://w.soundcloud.com/player/?url=https%3A//api.soundcloud.com/playlists/1234567&color=%23ff5500&auto_play=false&hide_related=false&show_comments=true&show_user=true&show_reposts=false&show_teaser=true',
    socialLinks: {
      soundcloud: 'https://soundcloud.com/atish',
      instagram: 'https://instagram.com/atish',
      residentAdvisor: 'https://ra.co/dj/atish'
    }
  },
};

export default function ArtistPage() {
  const params = useParams();
  const slug = params?.slug as string;
  const [artist, setArtist] = useState<any>(null);
  const [loading, setLoading] = useState(true);
  const [isMobile, setIsMobile] = useState(false);
  const animRef = useRef<HTMLDivElement>(null);

  // Handle responsive design
  useEffect(() => {
    const handleResize = () => {
      setIsMobile(window.innerWidth <= 768);
    };
    
    handleResize();
    window.addEventListener('resize', handleResize);
    return () => window.removeEventListener('resize', handleResize);
  }, []);

  useEffect(() => {
    const fetchArtist = async () => {
      setLoading(true);
      
      // Try to fetch from Strapi first
      const strapiArtist = await getArtistBySlug(slug);
      
      if (strapiArtist) {
        setArtist(strapiArtist);
      } else {
        // Fallback to sample data
        const sampleData = sampleArtistData[slug];
        if (sampleData) {
          setArtist({
            attributes: {
              name: sampleData.name,
              slug: slug,
              bio: sampleData.bio,
              image: sampleData.image,
              soundcloud: sampleData.soundcloud,
              video: sampleData.video,
              socialLinks: sampleData.socialLinks,
            }
          });
        }
      }
      setLoading(false);
    };

    fetchArtist();
  }, [slug]);

  // Animated color bar effect
  useEffect(() => {
    if (!animRef.current) return;
    
    const colors = ["#ffffff", "#e476ae", "#ef4137", "#1895d3", "#f8ec21", "#814199", "#12b258"];
    let delayTimer: NodeJS.Timeout | null = null;
    let durationTimer: NodeJS.Timeout | null = null;
    
    const tick = () => {
      if (!animRef.current) return;
      animRef.current.classList.remove("active");
      
      const getRandomIntInclusive = (min: number, max: number) => {
        min = Math.ceil(min);
        max = Math.floor(max);
        return Math.floor(Math.random() * (max - min + 1)) + min;
      };
      
      const newStyles = {
        backgroundColor: colors[getRandomIntInclusive(0, colors.length - 1)],
        left: `${getRandomIntInclusive(0, 98)}%`,
        height: `${getRandomIntInclusive(10, 90)}%`,
      };
      
      Object.assign(animRef.current.style, newStyles);
      
      delayTimer = setTimeout(() => {
        if (animRef.current) animRef.current.classList.add("active");
      }, 100);
      
      durationTimer = setTimeout(tick, getRandomIntInclusive(1500, 3000));
    };
    
    tick();
    
    return () => {
      if (delayTimer) clearTimeout(delayTimer);
      if (durationTimer) clearTimeout(durationTimer);
    };
  }, [artist]);

  if (loading) {
    return (
      <div style={{ textAlign: 'center', padding: '100px 0', color: 'white' }}>
        Loading...
      </div>
    );
  }

  if (!artist) {
    return (
      <div className="container-custom">
        <section style={{ marginTop: '40px', marginBottom: '60px' }}>
          <h1 style={{ marginBottom: '40px' }}>ARTIST NOT FOUND</h1>
          <div style={{ marginLeft: '69px', marginRight: '69px' }}>
            <p style={{ fontSize: '1.6rem', marginBottom: '30px' }}>
              Sorry, we couldn't find information for this artist.
            </p>
            <Link href="/artists" style={{ color: '#f8ec21' }}>
              ← Back to Artists
            </Link>
          </div>
        </section>
      </div>
    );
  }

  const imageUrl = artist.attributes.image;

  return (
    <main style={{ marginTop: isMobile ? '60px' : '70px' }}>
      <article>
        {/* Featured Image with animated bar */}
        <div style={{ 
          padding: 0, 
          margin: isMobile ? '0px 20px' : '0px 69px', 
          overflow: 'hidden' 
        }}>
          <div style={{ position: 'relative', overflow: 'hidden' }}>
            {/* Animated color bar */}
            <div 
              ref={animRef}
              className="anim"
              style={{
                width: '10px',
                height: '50%',
                top: '-100%',
                position: 'absolute',
                filter: 'blur(8px)',
                zIndex: 10
              }}
            />
            {imageUrl && (
              <img 
                src={imageUrl} 
                alt={artist.attributes.name}
                style={{ width: '100%', display: 'block' }}
              />
            )}
          </div>
        </div>

        {/* Artist Name */}
        <h1 style={{ marginTop: '20px' }}>
          {artist.attributes.name.toUpperCase()}
        </h1>

        {/* Content Area */}
        <div style={{ 
          display: isMobile ? 'block' : 'flex', 
          gap: '20px', 
          marginBottom: '60px' 
        }}>
          {/* Bio Text - Left Side */}
          <div style={{ 
            width: isMobile ? '100%' : '48%', 
            float: isMobile ? 'none' : 'left' 
          }}>
            <div style={{
              color: 'white',
              fontSize: '1.6rem',
              textAlign: 'left',
              marginLeft: isMobile ? '20px' : '69px',
              marginRight: isMobile ? '20px' : '69px',
              overflow: 'scroll',
              height: isMobile ? 'auto' : '850px',
              padding: isMobile ? '1em' : '0',
              msOverflowStyle: 'none',
              scrollbarWidth: 'none',
            }}>
              <p style={{ lineHeight: '1.8' }}>
                {artist.attributes.bio || 'No biography available yet.'}
              </p>
              
              {/* Social Links */}
              {artist.attributes.socialLinks && (
                <div style={{ marginTop: '40px' }}>
                  <h3 style={{ fontSize: '1.8rem', marginBottom: '20px' }}>Links</h3>
                  <div style={{ display: 'flex', flexDirection: 'column', gap: '15px' }}>
                    {artist.attributes.socialLinks.soundcloud && (
                      <a 
                        href={artist.attributes.socialLinks.soundcloud}
                        target="_blank"
                        rel="noopener noreferrer"
                        style={{ color: '#f8ec21', fontSize: '1.4rem' }}
                      >
                        → Soundcloud
                      </a>
                    )}
                    {artist.attributes.socialLinks.instagram && (
                      <a 
                        href={artist.attributes.socialLinks.instagram}
                        target="_blank"
                        rel="noopener noreferrer"
                        style={{ color: '#e476ae', fontSize: '1.4rem' }}
                      >
                        → Instagram
                      </a>
                    )}
                    {artist.attributes.socialLinks.residentAdvisor && (
                      <a 
                        href={artist.attributes.socialLinks.residentAdvisor}
                        target="_blank"
                        rel="noopener noreferrer"
                        style={{ color: '#1895d3', fontSize: '1.4rem' }}
                      >
                        → Resident Advisor
                      </a>
                    )}
                  </div>
                </div>
              )}
            </div>
          </div>

          {/* Widgets - Right Side */}
          <div style={{ 
            marginRight: isMobile ? '20px' : '69px',
            marginLeft: isMobile ? '20px' : '0',
            width: isMobile ? '100%' : '49%',
            marginTop: isMobile ? '20px' : '0'
          }}>
            {/* Video Widget */}
            {artist.attributes.video && (
              <iframe 
                title="video widget"
                src={artist.attributes.video}
                style={{
                  width: '100%',
                  height: isMobile ? (window.innerWidth <= 380 ? '220px' : '310px') : '380px',
                  marginBottom: '20px',
                  backgroundColor: 'black'
                }}
                frameBorder="0"
                allow="accelerometer; autoplay; encrypted-media; gyroscope; picture-in-picture"
                allowFullScreen
              />
            )}
            
            {/* SoundCloud Widget */}
            {artist.attributes.soundcloud && (
              <iframe 
                src={artist.attributes.soundcloud}
                title="sc widget"
                style={{
                  width: '100%',
                  height: isMobile ? (window.innerWidth <= 380 ? '160px' : '260px') : '320px',
                  backgroundColor: 'black'
                }}
                frameBorder="0"
                allowTransparency={true}
                allow="encrypted-media"
              />
            )}
            
            {/* If no widgets, show placeholder */}
            {!artist.attributes.video && !artist.attributes.soundcloud && (
              <div style={{
                width: '100%',
                height: '320px',
                backgroundColor: 'rgba(0,0,0,0.5)',
                display: 'flex',
                alignItems: 'center',
                justifyContent: 'center',
                border: '1px solid rgba(255,255,255,0.1)'
              }}>
                <p style={{ color: 'rgba(255,255,255,0.5)' }}>
                  Media content coming soon
                </p>
              </div>
            )}
          </div>
        </div>
      </article>


    </main>
  );
}